    background-color: #1C1717;
    color: wheat;
    font-family: 'Epilogue', sans-serif;
}

header {
    display: flex;
    justify-content: center;
    font-family: 'Atomic Age';
}

.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#addDiv {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

#addDiv > input {
    grid-column: 1 / 3;
}

.tasks {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.task {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    align-items: center;
    justify-items: center;
    margin: 5px;
    padding: 5px;
    border: 1px solid green;
}

.task > input {
    align-self: center;
    height: 5vh;
    width: 2vw;
}

.newTaskForm {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(6, 1fr);
    gap: 5px;
    height: 25vh;
}

#formTitle {
    grid-column: 1 / 6;
    grid-row: 1;
    justify-self: center;
    align-self: center;
}

#newTaskNameLabel {
    grid-column: 2 / 3;
    grid-row: 2;
    justify-self: center;
    align-self: center;
}

#newTaskName {
    grid-column: 3 / 5;
    grid-row: 2;
    align-self: center;
}

#newTaskDescriptionLabel {
    grid-column: 3 / 4;
    grid-row: 3;
    align-self: center;
    height: 50%;
}

#newTaskDescription {
    grid-column: 1 / 6;
    grid-row: 4;
}

#newTaskDueDateLabel {
    grid-column: 2 / 2;
    grid-row: 5;
    justify-self: center;
    align-self: center;
}

#newTaskDueDate {
    grid-column: 3 /  5;
    grid-row: 5;
    align-self: center;
}

#newTaskAddButton {
    grid-column: 3 /  3;
    grid-row: 6;
    justify-self: center;
    align-self: center;
    height: 75%;
    width: 75%;
}
